@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  touch-action: manipulation;
}

/* Increase font size for better readability for children */
html {
  font-size: 18px;
}

/* Disable text selection for better touch experience */
* {
  user-select: none;
}

/* Add some animation classes */
@keyframes pop {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.animate-pop {
  animation: pop 0.3s ease-in-out;
}

/* High contrast colors for better visibility */
:root {
  --primary-color: #4299e1; /* blue-500 */
  --secondary-color: #f6ad55; /* orange-400 */
  --accent-color: #f56565; /* red-500 */
  --background-color: #ebf8ff; /* blue-100 */
  --text-color: #2d3748; /* gray-800 */
}